body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
font-family: 'NeoBulletin Extruded Regular';
font-style: normal;
font-weight: normal;
src: local('NeoBulletin Extruded Regular'), url("/News/static/media/NeoBulletin Extruded.3e228e3f.woff") format('woff');
}
@font-face {
font-family: 'Syabab Brush Script Regular';
font-style: normal;
font-weight: normal;
src: local('Syabab Brush Script Regular'), url("/News/static/media/Syabab Brush Script-demo.83164b31.woff") format('woff');
}


.head{
  height:180px;
  background-color: rgba(0,0,0,.6);
  border: 2px solid lightgreen;
  border-radius: 5px;
display: flex;
justify-content:space-around;
align-items: center;
margin: 20px;
margin-top: 0px;

}
.body{
   border-radius: 5px;
}
.i{
  height:120px;
  width: auto;
}
#h{
  font-family: 'NeoBulletin Extruded Regular';
  font-size: 70px;
    color:white;
}
.foot{
font-size: 30px;
color:orange;

}
#name{
  font-family: 'Syabab Brush Script Regular';
  font-size: 50px;
  color:lightgreen;
  margin: 20px;
}
#foot{
  margin-bottom: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.load{
  color:orange;
  text-align: center;
}

#card{
	background-color: rgba(0,0,0,.8);
	margin: 10px;
	border: 1px rgba(255,255,255,.5) solid;
	border-radius: 5px;
}

#ari{
	
	height:auto;
	width:45vh;
margin: 10px;
    box-shadow: -1px 1px 2px black;
    padding: 5px;
    border-radius: 15px;
    float: left;
}
#title{
	margin-top: 10px;
	font-size: 35px;
	font-family: Times-New-Roman;
	color:orange;
	margin: 5px;
}
#desc,#cont{
	color:white;
	font-size: 22px;
	padding: 10px;
}
#link{
	color: white;
	float: right;
}
a{
	color:white;
	text-decoration: none;
	font-family: cursive;
	margin:10px;
	font-size: 16px;
	border:1px solid white;
	border-radius: 5px;
	padding: 0px 3px;
}
.end{
	display: flex;
	justify-content: space-around;
	padding: 10px;
	margin-left: 120px;
	margin-top: 0px;
}
.end p{
	color:white;
	font-size: 20px;
	padding:10px;
}
